/* Add Letter Modal Styles */
.modal {
  z-index: 1055;
}

.modal.show {
  display: block !important;
}

.modal-backdrop.show {
  opacity: 0.5;
}

.modal-dialog {
  max-width: 800px;
}

.modal-header {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 1rem 1.5rem;
  flex-direction: row;
  display: flex;
  align-items: center;
}

.modal-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
  display: flex;
  align-items: center;
}

/* RTL Support for modal header */
.modal-header[dir="rtl"] {
  justify-content: flex-end;
}

.modal-header[dir="ltr"] {
  justify-content: flex-start;
}

/* RTL Support for modal title */
.modal-title[class*="text-end"] {
  text-align: right !important;
  margin-left: auto !important;
  margin-right: 0 !important;
  order: 2;
}

.modal-title[class*="text-start"] {
  text-align: left !important;
  margin-left: 0 !important;
  margin-right: auto !important;
  order: 1;
}

/* Force RTL layout for Arabic */
.modal-header[dir="rtl"] .modal-title {
  margin-left: auto !important;
  margin-right: 0 !important;
  text-align: right !important;
}

/* Force LTR layout for English */
.modal-header[dir="ltr"] .modal-title {
  margin-left: 0 !important;
  margin-right: auto !important;
  text-align: left !important;
}
.modal-body {
  padding: 1.5rem;
  max-height: 70vh;
  overflow-y: auto;
}

.modal-footer {
  border-top: 1px solid #dee2e6;
  padding: 1rem 1.5rem;
  background-color: #f8f9fa;
}

/* Form styling */
.form-label {
  font-weight: 500;
  color: #495057;
  margin-bottom: 0.5rem;
}

.form-control, .form-select {
  border: 1px solid #ced4da;
  border-radius: 0.375rem;
  padding: 0.5rem 0.75rem;
  font-size: 0.875rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-control:focus, .form-select:focus {
  border-color: #043DFF;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
  outline: 0;
}

.form-control.is-invalid, .form-select.is-invalid {
  border-color: #dc3545;
}

.form-control.is-invalid:focus, .form-select.is-invalid:focus {
  border-color: #dc3545;
  box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25);
}

.invalid-feedback {
  display: block;
  width: 100%;
  margin-top: 0.25rem;
  font-size: 0.875rem;
  color: #dc3545;
}

/* Textarea specific styling */
textarea.form-control {
  resize: vertical;
  min-height: 100px;
}

/* Button styling */
.btn {
  border-radius: 0.375rem;
  font-weight: 500;
  padding: 0.5rem 1rem;
  transition: all 0.15s ease-in-out;
}

.btn-primary {
  background-color: #043DFF;
  border-color: #043DFF;
  color: #fff;
}

.btn-primary:hover:not(:disabled) {
  background-color: #0056b3;
  border-color: #0056b3;
}

.btn-primary:disabled {
  background-color: #6c757d;
  border-color: #6c757d;
  opacity: 0.65;
}

.btn-secondary {
  background-color: #6c757d;
  border-color: #6c757d;
  color: #fff;
}

.btn-secondary:hover {
  background-color: #545b62;
  border-color: #4e555b;
}

/* Required field indicator */
.text-danger {
  color: #dc3545 !important;
}

/* Grid spacing */
.row.g-3 > * {
  padding: 0.75rem;
}

/* Icon styling */
.fas {
  font-size: 0.875rem;
}

/* Close button */
.btn-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  opacity: 0.5;
  transition: opacity 0.15s ease-in-out;
}

.btn-close:hover {
  opacity: 0.75;
}

.btn-close:focus {
  outline: none;
  box-shadow: none;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .modal-dialog {
    margin: 0.5rem;
    max-width: calc(100% - 1rem);
  }
  
  .modal-body {
    padding: 1rem;
    max-height: 60vh;
  }
  
  .modal-header, .modal-footer {
    padding: 0.75rem 1rem;
  }
  
  .row.g-3 > * {
    padding: 0.5rem;
  }
}

/* RTL Support */
[dir="rtl"] .modal-header {
  text-align: right;
}

[dir="rtl"] .me-2 {
  margin-right: 0 !important;
  margin-left: 0.5rem !important;
}

[dir="rtl"] .ms-2 {
  margin-left: 0 !important;
  margin-right: 0.5rem !important;
}

/* Animation for modal */
.modal.fade .modal-dialog {
  transition: transform 0.3s ease-out;
  transform: translate(0, -50px);
}

.modal.show .modal-dialog {
  transform: none;
}

/* Form validation states */

.form-control:valid:focus {
  border-color: #28a745;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.25);
}

/* Loading state for submit button */
.btn:disabled {
  cursor: not-allowed;
}

/* Custom scrollbar for modal body */
.modal-body::-webkit-scrollbar {
  width: 6px;
}

.modal-body::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* RTL Dropdown Arrow Alignment */
[dir="rtl"] .form-select {
  background-position: left 0.75rem center;
  padding-left: 2.25rem;
  padding-right: 0.75rem;
}

[dir="ltr"] .form-select {
  background-position: right 0.75rem center;
  padding-right: 2.25rem;
  padding-left: 0.75rem;
}

/* Ensure dropdown arrows don't overlap with text */
[dir="rtl"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

[dir="ltr"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

/* Focus state for RTL dropdowns */
[dir="rtl"] .form-select:focus {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select:focus {
  background-position: right 0.75rem center;
}

/* Invalid state for RTL dropdowns */
[dir="rtl"] .form-select.is-invalid {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select.is-invalid {
  background-position: right 0.75rem center;
}